

import { MdSpaceDashboard ,MdProductionQuantityLimits,MdDeliveryDining  } from "react-icons/md";
import { FaShoppingBag } from "react-icons/fa";
import { GiReturnArrow } from "react-icons/gi";
 import { MdReport } from "react-icons/md";
import { Link, useLocation } from "react-router-dom";
import { useContext, useEffect, useState } from "react";
import axios from "axios";
import DataContext from "../usecontext/DataContext";

export default  function Courier1 (params) {

  // fetchallcourier
  const [data, setData] = useState([]);
  const [courierdata, setcourierdata] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const apiKey = "https://bwithf.onrender.com"
  const { userrole } = useContext(DataContext);


  useEffect(() => {
      fetchcourier()
      fetchcourierdetail()
  }, []);

   let fetchcourier = ()=>{
    let token = localStorage.getItem('token')

      axios.get(`${apiKey}/api/courier/fetchallcourier`,{
        method: 'GET',
        headers:{
             
            'authtoken': token
        },
    })
      .then(response => {
          setLoading(false);
        setData(response.data);
       })
      .catch(error => {
        setError(error);
        setLoading(false);
      });
  }

  
  let fetchcourierdetail = ()=>{
    let token = localStorage.getItem("token")
      axios.get(`${apiKey}/api/courier/fetchallcourierdetail`,{
        method: 'GET',
        headers:{
             
            'authtoken': token
        },
    })
      .then(response => {
          setLoading(false);
          setcourierdata(response.data);
       })
      .catch(error => {
        setError(error);
        setLoading(false);
      });
  }
   



  const [editingId, setEditingId] = useState(null);
  const [newName, setNewName] = useState('');

  const handleEditClick = (person) => {
    setEditingId(person._id);
    //console.log(person)
    setNewName(person.name); // Pre-fill the input with the current name
  };

  const handleInputChange = (e) => {
    setNewName(e.target.value);
    //console.log(e.target.value)
  };

  const handleEditSubmit = async (id) => {
    try {    let token = localStorage.getItem("token")

        // Define the headers including the authtoken
        const headers = {
            'authtoken': token
        };

        // Make an API call to update the product
        const response = await axios.put(`${apiKey}/api/courier/updatecourier/${id}`, {
            name: newName,
        }, { headers }); // Add headers here

        // Update the local state if the API call is successful
        if (response.status === 200) {
            setData((prevData) =>
                prevData.map((product) =>
                    product.id === id ? { ...product, name: newName } : product
                )
            );
            setEditingId(null);
            fetchcourier();
            setNewName('');
        } else {
            console.error('Failed to update product:', response.status);
        }
    } catch (error) {
        console.error('Error updating product:', error);
    }
};








 let deleteproduct = (id)=>{
  let token = localStorage.getItem("token")
   axios.delete(`${apiKey}/api/courier/deletecourier/${id}`,{
    method: 'DELETE',
    headers:{
         
        'authtoken': token
    },
})

   fetchcourier()
}

let confirm11 =(id)=>{
 if (window.confirm("Do you really want to delete?")) {
  deleteproduct(id)       
  fetchcourier()

    }
   
}

 

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error: {error.message}</p>;
   
    return(
        <>
       <div className="  ">
         
      
       <div className="space-y-5 h-[500px] overflow-y-scroll pb-3 mt-3">
  <div>
    <div className="flex flex-wrap gap-2 md:gap-1">
      {data?.map((person, index) => (
        <div
          key={person.id}
          className={`px-5 py-4 flex items-center justify-between text-gray-800 shadow-lg rounded-lg transition-transform transform hover:scale-105 duration-300 ${
            index % 3 === 0
              ? 'bg-gradient-to-tr from-blue-400 via-purple-400 to-pink-400'
              : index % 3 === 1
              ? 'bg-gradient-to-tr from-yellow-400 via-orange-400 to-red-400'
              : 'bg-gradient-to-tr from-green-400 via-teal-400 to-cyan-400'
          }`}
        >
          <div className="flex-1">
            {userrole.canEdit && editingId === person._id ? (
              <div className="flex items-center">
                <input
                  type="text"
                  value={newName}
                  onChange={handleInputChange}
                  className="border border-gray-300 px-3 py-2 font-semibold md:text-sm text-center text-[16px] bg-white text-gray-800 rounded-lg shadow-sm"
                />
                <button
                  onClick={() => handleEditSubmit(person._id)}
                  className="ml-2 bg-blue-500 hover:bg-blue-600 text-white font-bold py-1 px-2 rounded-lg"
                >
                  Save
                </button>
              </div>
            ) : (
              <div className="font-semibold md:text-sm text-center text-[16px] text-white">
                {person.name}
                {userrole.canEdit && (
                  <button
                    onClick={() => handleEditClick(person)}
                    className="ml-2 bg-yellow-500 hover:bg-yellow-600 text-white font-bold py-1 px-2 rounded-lg"
                  >
                    Edit
                  </button>
                )}
              </div>
            )}
          </div>
          {userrole.canDelete && (
            <button
              onClick={() => confirm11(person._id)}
              className="ml-4 bg-red-500 hover:bg-red-600 h-8 text-white font-bold py-1 px-2 rounded-lg"
            >
              Delete
            </button>
          )}
        </div>
      ))}
    </div>

    <div className="flex flex-wrap gap-4 mt-6">
      {courierdata?.map((person, index) => (
        person._id && (
          <div
            key={person._id}
            className={`shadow-lg rounded-lg p-4 w-full md:w-[45%] lg:w-[32%] transition-transform transform hover:scale-105 duration-300 ${
              index % 3 === 0
                ? 'bg-gradient-to-tr from-pink-300 via-purple-400 to-indigo-500'
                : index % 3 === 1
                ? 'bg-gradient-to-tr from-yellow-300 via-green-400 to-teal-500'
                : 'bg-gradient-to-tr from-blue-300 via-purple-400 to-pink-500'
            }`}
          >
            <div className="text-white font-semibold text-lg mb-2">{person._id}</div>
            <div className="space-y-3 text-white">
              <div className="flex justify-between">
                <div className="font-medium">Total Shipment</div>
                <div>{person.totalSalesQ}</div>
              </div>
              <div className="flex justify-between">
                <div className="font-medium">Total Delivered</div>
                <div>{person.totaldeliverdQ}</div>
              </div>
              <div className="flex justify-between">
                <div className="font-medium">Total Lost</div>
                <div>{person.totalLOSTQ}</div>
              </div>
              <div className="flex justify-between">
                <div className="font-medium">Total RTO</div>
                <div>{person.totalRTOQ}</div>
              </div>
            </div>
          </div>
        )
      ))}
    </div>
  </div>
</div>

        </div>
        </>
    )
}

